          </div>
        </div>
      </div>

      <!-- Empty state -->
      <div v-else-if="selectedUserId && !loading" class="px-6 py-12 text-center">
        <div class="text-gray-400 mb-4">
          <svg class="mx-auto h-12 w-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"></path>
          </svg>
        </div>
        <h3 class="text-lg font-medium text-gray-900 mb-2">No approvals found</h3>
        <p class="text-gray-600">
          {{ filteredApprovals.length === 0 && approvals.length > 0 
            ? 'No approvals match the current filters.' 
            : 'No approval requests are assigned to this user.' 
          }}
        </p>
      </div>

      <!-- No user selected state -->
      <div v-else-if="!selectedUserId" class="px-6 py-12 text-center">
        <div class="text-gray-400 mb-4">
          <svg class="mx-auto h-12 w-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z"></path>
          </svg>
        </div>
        <h3 class="text-lg font-medium text-gray-900 mb-2">Select a user</h3>
        <p class="text-gray-600">Please select a user from the dropdown above to view their approval requests.</p>
      </div>
    </div>

    <!-- Recent Activity -->
    <div class="bg-white rounded-lg border border-gray-200">
      <div class="px-6 py-4 border-b border-gray-200">
        <h2 class="text-lg font-semibold text-gray-900">Recent Activity</h2>
      </div>
      <div class="px-6 py-4">
        <div v-if="recentActivity.length > 0" class="space-y-3">
          <div 
