</template>

<script setup lang="ts">
import { ref, onMounted, computed, watch } from 'vue'
import { API_CONFIG, API_ENDPOINTS, buildApiUrl, apiCall } from '@/config/api'

// Note: This is an MPA (Multi-Page Application), not an SPA
// Navigation is handled via window.location.href, not Vue Router

// Reactive data
const approvals = ref([])
const users = ref([])
const loading = ref(false)
const processingStage = ref('')
const selectedUserId = ref('')
const statusFilter = ref('all')
const priorityFilter = ref('all')
const typeFilter = ref('all')
const showDetailsModal = ref(false)
const selectedApproval = ref(null)

// Computed properties
const filteredApprovals = computed(() => {
  let filtered = approvals.value

  if (statusFilter.value !== 'all') {
    filtered = filtered.filter(approval => approval.stage_status === statusFilter.value)
  }

  if (priorityFilter.value !== 'all') {
    filtered = filtered.filter(approval => approval.priority === priorityFilter.value)
  }

  if (typeFilter.value !== 'all') {
    filtered = filtered.filter(approval => approval.business_object_type === typeFilter.value)
  }

  return filtered
})

const recentActivity = computed(() => {
  // Get completed approvals (approved or rejected) and sort by completion date
  return approvals.value
    .filter(approval => approval.stage_status === 'APPROVED' || approval.stage_status === 'REJECTED')
    .sort((a, b) => {
      const dateA = new Date(a.completed_at || a.updated_at)
      const dateB = new Date(b.completed_at || b.updated_at)
      return dateB.getTime() - dateA.getTime()
    })
    .slice(0, 5) // Show only the 5 most recent activities
})

// Methods
const fetchUsers = async () => {
  try {
    const url = buildApiUrl(API_ENDPOINTS.RFP_APPROVAL.USERS)
    const data = await apiCall(url)
    users.value = data
  } catch (error) {
    console.error('Error fetching users:', error)
    // Fallback to mock data if API fails
    users.value = [
      {
        id: '1',
        username: 'admin',
        first_name: 'Admin',
        last_name: 'User',
        email: 'admin@company.com',
        role: 'Administrator',
        department: 'IT',
        is_active: true
      },
      {
        id: '2',
        username: 'manager',
        first_name: 'Manager',
        last_name: 'User',
        email: 'manager@company.com',
        role: 'Manager',
        department: 'Operations',
        is_active: true
      },
      {
        id: '3',
        username: 'reviewer',
        first_name: 'Reviewer',
        last_name: 'User',
        email: 'reviewer@company.com',
        role: 'Reviewer',
        department: 'Finance',
        is_active: true
      }
    ]
  }
}

const fetchApprovals = async () => {
  if (!selectedUserId.value) {
    approvals.value = []
    return
  }

  loading.value = true
  try {
    const url = buildApiUrl(`${API_ENDPOINTS.RFP_APPROVAL.USER_APPROVALS}?user_id=${selectedUserId.value}`)
    const data = await apiCall(url)
    
    // Force Vue reactivity by creating a new array
    approvals.value = [...data]
  } catch (error) {
    console.error('Error fetching approvals:', error)
    // Show user-friendly error message
    alert('Failed to fetch approvals. Please check if the backend server is running on port 8000.')
    approvals.value = []
  } finally {
    loading.value = false
  }
}

const onUserChange = () => {
  if (selectedUserId.value) {
    fetchApprovals()
  } else {
    approvals.value = []
  }
}

const applyFilters = () => {
  // Filters are applied automatically through computed property
}

const handleApprove = async (stageId: string) => {
  processingStage.value = stageId
  try {
    const url = buildApiUrl(API_ENDPOINTS.RFP_APPROVAL.UPDATE_STAGE_STATUS)
    await apiCall(url, {
      method: 'POST',
      body: JSON.stringify({
        stage_id: stageId,
        status: 'APPROVE',
        comments: 'Approved by user'
      })
    })

    // Refresh the approvals list
    await fetchApprovals()
    console.log('Approval successful')
  } catch (error) {
    console.error('Error approving:', error)
    alert('Failed to approve. Please try again.')
  } finally {
    processingStage.value = ''
  }
}

const handleReject = async (stageId: string) => {
  const reason = prompt('Please provide a reason for rejection:')
  if (!reason) return

  processingStage.value = stageId
  try {
    const url = buildApiUrl(API_ENDPOINTS.RFP_APPROVAL.UPDATE_STAGE_STATUS)
    await apiCall(url, {
      method: 'POST',
      body: JSON.stringify({
        stage_id: stageId,
        status: 'REJECT',
        comments: reason
      })
    })

    // Refresh the approvals list
    await fetchApprovals()
    console.log('Rejection successful')
  } catch (error) {
    console.error('Error rejecting:', error)
    alert('Failed to reject. Please try again.')
  } finally {
    processingStage.value = ''
  }
}

const handleViewDetails = (approval: any) => {
  try {
    // MPA Navigation: Use window.location.href for navigation
    const queryParams = new URLSearchParams({
      userId: selectedUserId.value,
      stageId: approval.stage_id,
      approvalId: approval.approval_id,
      workflowId: approval.approval_id
    })
    
    // Try to navigate to the StageReviewer page
    // Since this is an MPA, we need to check if the StageReviewer page exists
    const possiblePaths = [
      '/stage-reviewer.html',
      '/rfp-approval/stage-reviewer.html',
      '/approval-management.html'
    ]
    
    // Navigate to the StageReviewer page
    console.log('Navigating to StageReviewer with params:', {
      userId: selectedUserId.value,
      stageId: approval.stage_id,
      approvalId: approval.approval_id,
      workflowId: approval.approval_id
    })
    
    // Navigate to the stage-reviewer page
    window.location.href = `/stage-reviewer.html?${queryParams.toString()}`
    
  } catch (error) {
    console.error('Error in handleViewDetails:', error)
    // Fallback to modal
    selectedApproval.value = approval
    showDetailsModal.value = true
  }
}

const getStatusColor = (status: string) => {
  // Convert to uppercase for case-insensitive comparison
  const statusUpper = status?.toUpperCase() || '';
  
  switch (statusUpper) {
    case 'PENDING':
      return 'bg-yellow-500'
    case 'IN_PROGRESS':
      return 'bg-blue-500'
    case 'APPROVED':
      return 'bg-green-500'
    case 'REJECTED':
      return 'bg-red-500'
    case 'EXPIRED':
      return 'bg-gray-500'
    default:
      return 'bg-gray-400'
  }
}

const getStatusBadgeClass = (status: string) => {
  // Convert to uppercase for case-insensitive comparison
  const statusUpper = status?.toUpperCase() || '';
  
  switch (statusUpper) {
    case 'PENDING':
      return 'bg-yellow-100 text-yellow-800'
    case 'IN_PROGRESS':
      return 'bg-blue-100 text-blue-800'
    case 'APPROVED':
      return 'bg-green-100 text-green-800'
    case 'REJECTED':
      return 'bg-red-100 text-red-800'
    case 'EXPIRED':
      return 'bg-gray-100 text-gray-800'
    default:
      return 'bg-gray-100 text-gray-800'
  }
}

const formatDate = (dateString: string) => {
  if (!dateString) return 'N/A'
  try {
    const date = new Date(dateString)
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString()
  } catch (error) {
    return 'Invalid Date'
  }
}

const formatRelativeTime = (dateString: string) => {
  if (!dateString) return 'N/A'
  try {
    const date = new Date(dateString)
    const now = new Date()
    const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000)
    
    if (diffInSeconds < 60) {
      return 'Just now'
    } else if (diffInSeconds < 3600) {
      const minutes = Math.floor(diffInSeconds / 60)
      return `${minutes} minute${minutes > 1 ? 's' : ''} ago`
    } else if (diffInSeconds < 86400) {
      const hours = Math.floor(diffInSeconds / 3600)
      return `${hours} hour${hours > 1 ? 's' : ''} ago`
    } else {
      const days = Math.floor(diffInSeconds / 86400)
      return `${days} day${days > 1 ? 's' : ''} ago`
    }
  } catch (error) {
    return 'Invalid Date'
  }
}

// Watchers - removed debug watchers

// Lifecycle
onMounted(() => {
  fetchUsers()
})
</script>

